.login {
	display: grid;
	place-items: center;
	margin-left: auto;
	margin-right: auto;
	padding-top: 100px;
	padding-bottom: 100px;
}
.login > img {
	object-fit: cover;
	height: 70px;
	margin-top: 20px;
	margin-bottom: 20px;
}
.login > form {
	display: flex;
	flex-direction: column;
}
.login > form > input {
	width: 350px;
	height: 52px;
	font-size: 20px;
	padding-left: 10px;
	margin-bottom: 10px;
	border-radius: 5px;
}
.login > form > button {
	width: 365px;
	height: 50px;
	font-size: large;
	color: #fff;
	background-color: #00669a;
	border-radius: 5px;
}
.login > form > button:hover {
	cursor: pointer;
	opacity: 90%;
}
.login__register {
	color: #00669a;
	cursor: pointer;
}
.login > p {
	margin-top: 20px;
}
