.sidebar {
	position: sticky;
	top: 80px;
	flex: 0.2;
	border-radius: 10px;
	text-align: center;
	height: fit-content;
}
.sidebar__avatar {
	margin-bottom: 10px;
}
.sidebar__top {
	display: flex;
	flex-direction: column;
	align-items: center;
	border: 1px solid lightgray;
	border-bottom: 0px;
	border-top-right-radius: 10px;
	border-top-left-radius: 10px;
	background-color: #fff;
	padding-bottom: 10px;
}
.sidebar__top {
	overflow: hidden;
}
.sidebar__top > img {
	margin-bottom: -20px;
	width: 250px;
	height: 60px;
	object-fit: cover;
}
.sidebar__top > h4 {
	color: gray;
	font-size: 12px;
}
.sidebar__top > h2 {
	font-size: 18px;
}
.sidebar__stat {
	margin-top: 10px;
	display: flex;
	justify-content: space-between;
}
.sidebar__statText {
	color: gray;
	font-size: 13px;
	font-weight: 600;
}
.sidebar__statNumber {
	color: dodgerblue;
	font-weight: bold;
}
.sidebar__stats {
	padding: 10px;
	margin-bottom: 10px;
	border: 1px solid lightgray;
	border-bottom-left-radius: 10px;
	background-color: #fff;
	border-bottom-right-radius: 10px;
}
.sidebar__bottom {
	text-align: left;
	padding: 10px;
	border: 1px solid lightgray;
	background-color: white;
	border-radius: 10px;
	margin-top: 10px;
}
.sidebar__recentItem {
	display: flex;
	font-size: 13px;
	color: gray;

	margin-bottom: 5px;
	padding: 5px;
	font-weight: bolder;
}
.sidebar__recentItem:hover {
	cursor: pointer;
	background-color: whitesmoke;
	border-radius: 10px;
	color: black;
}
.recent__p {
	font-size: 16px;
	font-weight: 600;
	padding-bottom: 10px;
}
