.feed {
	flex: 0.6;
	margin: 0px 20px;
	min-height: 95vh;
}
.feed__inputContainer {
	display: flex;
	flex-direction: column;
	background-color: white;
	border-radius: 10px;
	margin-bottom: 20px;
	padding-bottom: 15px;
}
.feed__input {
	border: 1px solid lightgray;
	border-radius: 30px;
	display: flex;
	margin: 15px;
	margin-bottom: 0px;
	flex: 1;
	padding: 10px;
	color: gray;
	padding-left: 15px;
}
.feed__input > form {
	display: flex;
	width: 100%;
}
.feed__input > form > input {
	border: none;
	flex: 1;
	margin-left: 10px;
	outline-width: 0;
	font-weight: 600;
}
.feed__input > form > button {
	display: none;
}
.feed__inputOptions {
	display: flex;
	justify-content: space-evenly;
}
